@keyframes build-detail-appear{from{opacity:0}to{opacity:1}}.build-detail-wrapper{animation-name:build-detail-appear;animation-duration:.5s;animation-fill-mode:both;position:fixed;top:0px;left:0px;width:100%;height:100%;background-color:rgba(0,0,0,.473);z-index:3;display:flex;align-items:center;justify-content:center}.build-detail{width:80%;height:90%;background-color:#2a2a2a;border-radius:1em;z-index:4;display:flex;flex-direction:column;align-items:center;overflow-y:scroll}.build-detail::-webkit-scrollbar-thumb{background-color:#d8d8d8;border-radius:.2em}.build-detail::-webkit-scrollbar-track{background-color:#5e5e5e;border-radius:.2em}.build-detail::-webkit-scrollbar{width:15px}.hidebutton{position:absolute;width:25px;height:25px;margin:0px 10px 0px 0px;top:21%;right:15%;background-color:red;border:2px solid #d9d9d9;border-radius:20%;z-index:5}.hidebutton::before{content:"×";position:relative;top:-16px;font-size:37px;color:#fff;transform:rotote(45deg)}.hidebutton:hover{cursor:pointer}.left{align-self:flex}